import {request} from "@/utils/request.js"

let baseUrl = "/user/column"

const column = {
	/**
	 *  	
	 * @param {Object} userId	
	 */
	getPage(args){
		return request({
			url: baseUrl+"/page/"+args.userId,
			method:"GET",
			data: {
				page: args.page,
				size: args.size
			}
		});
	},
	
	/**
	 *  	
	 * @param {Object} userId	
	 */
	getAllByUserId(userId){
		return request({
			url: baseUrl+"/all/"+userId,
			method:"GET",
		});
	},
	
	
	
	/**	
	 * 
	 * @param {Object} id	
	 */
	getOne(id){
		return request({
			url: baseUrl+"/"+id,
			method:"GET"
		});
	}
	
	
	
}


export default column;