import {request} from "@/utils/request.js"
	

let baseUrl = '/article'


const article = {
	/**
	 * 	
	 * @param {Object} data
	 */
	publicArticle(data){
		return request({
			url: baseUrl + '/article',
			method:"POST",
			data:data
		});
	},
	
	/**
	 * 	
	 */
	getArticlePage(args){
		return request({
			url: baseUrl + '/article/page',
			method:"Get",
			data:args
		});
	},
	
	/**
	 * 	
	 */
	getDetailById(id){
		return request({
			url: baseUrl + '/article/'+id,
			method:"Get"
		});
	}
	
}

export default article