import {request} from "@/utils/request.js"

let baseUrl = "/user"

const user = {
	
	/**		
	 * @param {Object} args 
	 * 
	 */
	login(args){
		return request({
			url: baseUrl + '/user/login',
			method:"POST",
			data: args
		});
	},
	
	/**
	 *   
	 * @param {Object} args	
	 */
	register(args){
		return request({
			url: baseUrl + '/user/save',
			method:"POST",
			data: args
		});
	}
	
}


export default user