import {request} from "@/utils/request.js"

let baseUrl = '/system'

const label = {
	/**
	 * 	
	 */
	getPage(condition){
		return request({
			url: baseUrl + '/label/app/page',
			method:"Get",
			data:condition
		});
	},
	
	/**
	 * 	 10
	 */
	getSelectLabel(arg){
		return request({
			url: baseUrl + '/label/page',
			method:"Get",
			data:{
				name:arg,
				page:0,
				size:10
			}
		});
	}
	
	
}


export default label;