import {request} from "@/utils/request.js"

let baseUrl = "/boiling"

const boiling = {
	/**
	 *   
	 */
	getPageCondition(args){
		return request({
			url: baseUrl+"/boiling/page",
			method:"GET",
			data:args
		});
	},
	
	/**
	 * 	
	 */
	publishBoilling(args){
		return request({
			url: baseUrl + "/boiling",
			method:"POST",
			data:args
			//header: {'content-type': 'multipart/form-data'},
		});
	}
	
	
}


export default boiling;