import {request} from "@/utils/request.js"

let baseUrl = "/focus"

const focus = {
	/**
	 * 	
	 * @param {Object} condition
	 */
	postFocus(condition){
		return request({
			url: baseUrl+'/focus',
			method:"POST",
			data:condition,
			header: {'content-type': 'application/x-www-form-urlencoded'},
		});
	},
	
	/**
	 * 	
	 * @param {Object} condition
	 */
	cancelFocus(condition){
		return request({
			url: baseUrl+'/focus/'+condition.type+'/'+condition.id,
			method:"Delete"
		});
	}
	
	
	
	
}


export default focus;