import {request} from "@/utils/request.js"


let baseUrl = "/user/collection"

const favorities = {
	/**
	 * 	
	 * @param {Object} args	
	 */
	save(args){
		return request({
			url: baseUrl,
			method:"POST",
			data:args
		});
	},
	
	/**
	 * 	
	 */
	getAll(args){
		return request({
			url: baseUrl + '/'+args.type+'/'+args.userId,
			method:"GET"
		});
	},
	
	/**	target
	 * @param {Object} args
	 */
	getTargetListByFavoritiesId(args){
		return request({
			url: baseUrl + '/'+args.id,
			method:"GET",
			data:{
				page:args.page,
				size:args.size,
				type:args.type,
			}
		});
	},
	
	/**
	 * 	 
	 */
	getOne(id){
		return request({
			url: baseUrl + '/getOne',
			method:"GET",
			data:{
				id:id
			}
		});
	},
	
	/**
	 *   
	 */
	addCollection(args){
		return request({
			url: baseUrl + '/'+args.favoritiesId+'/'+args.collectionType+'/'+args.collectionId,
			method:"POST"
		});
	},
	
	/**
	 *   
	 */
	cancelCollection(args){
		return request({
			url: baseUrl + '/'+args.favoritiesId+'/'+args.collectionType+'/'+args.collectionId,
			method:"DELETE"
		});
	},
	
	/**
	 * 	id
	 */
	getFavoritiesByCollection(args){
		return request({
			url: baseUrl+'/getFavoritiesId',
			method:"GET",
			data:args
		});
	}
	
}



export default favorities;

