import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import $config from "@/utils/config.js"


export default new Vuex.Store({
	// =======================state========================================
    state:{
		// user info
		loginStatus: false,
		loginUser:{}, 
		token:"",
		
		// socket
		isConnect: false ,
		SocketTask: false,   // new WebSocket
		isOnline: false,    // 
		
		// 
		ToUser:{
			id:999, // user_id0
			userNickname:"tangtanwei",
			userAvatar:"",
			status: ""      // ( ,,)
		},
		
		// 
		chatList:[]
	},
	// =======================mutations============this.$store.commit('login',user);============================
	mutations:{
		// 
		login(state,res){
			state.loginStatus = true;
			state.loginUser = res.user
			state.token = res.access_token
			uni.setStorageSync('user', JSON.stringify(res.user)); //
			uni.setStorageSync('token',res.access_token); //	
		},
		// 
		logout:(state) => {
			state.loginStatus = false;
			state.loginUser = {}
			uni.removeStorageSync('user')
			uni.removeStorageSync('token')
		}
	},
	// =======================actions()======	this.$store.dispatch('openSocket');============
	actions:{
		// 1 - socket
		openSocket({ state,dispatch }){
			// 
			if(state.isConnect) return
			
			// 
			state.SocketTask = uni.connectSocket({
			    url: $config.WsUrl,
			    complete: ()=> {
					console.log("ws");
				}
			});
			
			
			if (!state.SocketTask){
				console.log("SocketTask");
				return;
			} ;
			
			
			
			// 
			state.SocketTask.onOpen(()=>{
				// 
				console.log('');
				state.isConnect = true
			})
			
			console.log("AAAAAAAAAAAAAAAAAAAAAAAAA:::"+state.isConnect);
			
			
			// 
			state.SocketTask.onClose(()=>{
				console.log('');
				state.isConnect  = false;
				state.SocketTask = false;
				state.IsOnline = false
				// 
				// 
			})
			
			// 
			state.SocketTask.onError(()=>{
				console.log('');
				state.isConnect = false;
				state.SocketTask = false;
				state.IsOnline = false
			})
			
			// 
			state.SocketTask.onMessage((e)=>{
				console.log('',e);
				// json
				let res = JSON.parse(e.data);
				// 
				if (res.type == 'bind'){
					// 
					return dispatch('userBind',res.data)
				}
				// 
				if (res.type !== 'text') return;
				/*
				{
					to_id:1,      //  
					from_id:12,	  // id
					from_username:"",  // 
					from_userpic:"http://pic136.nipic.com/file/20170725/10673188_152559977000_2.jpg",
					type:"text",	 // 
					data:"",	 // 
					time:151235451   // 
				}
				*/
			   // 
			   dispatch('handleChatMessage',res)
			})
		},
			
			
		// 2- 
		handleChatMessage({state,dispatch},data){
			console.log('',data);
			// 
			uni.$emit('UserChat',data);
			// chatdetail
			dispatch('updateChatDetailToUser',{
				data,
				send:false
			})
			// 
			dispatch('updateChatList',data)
		},	
		
	},
	// =======================getters========================================
	
	getters:{
		// 
		totalNoread(state){
			let total = 0
			state.chatList.forEach(item=>{
				total += item.noread
			})
			return total
		}
	}	
});